tartTime: '',\n      showNotification: this.show\n    };\n  },\n  computed: {\n    showing: {\n      get() {\n        return this.showNotification;\n      },\n      set(newVal) {\n        this.showNotification = newVal;\n      }\n    },\n    calculatedTime(){\n      let time = (this.currentTime - this.startTime)/1000;\n      return this.formatTime(time);\n    }\n  },\n  methods: {\n    updateTime(){\n      this.currentTime = new Date().getTime();\n    },\n    formatTime(time){\n      switch (true) {\n        case (time === 0):\n          return `now`;\n        case (time < 60):\n          let second = Math.floor(time);\n          return `${second} seconds ago`;\n        case (time < 120):\n          return `1 minute ago`;\n        case (time < 3600):\n          let minutes = Math.floor(time/60);\n          return `${minutes} minutes ago`;\n        case (time === 3600):\n          return `1 hour ago`;\n        case (time > 3600 && time < 86400):\n          let hours = Math.floor(time/3600);\n          return `${hours} hours ago`;\n        case (time === 86400):\n          return `a day ago`;\n        case (time > 86400):\n          let days = Math.floor(time/86400);\n          return `${days} days ago`;\n      }\n    },\n    closeToast(){\n      this.showing = false;\n    },\n  },\n  mounted(){\n    let timeReceived = this.received || new Date();\n    this.startTime = timeReceived.getTime();\n    this.currentTime = new Date().getTime();\n    window.setInterval(this.updateTime, 60000);\n  },\n  beforeDestroy(){\n    window.clearInterval(this.updateTime);\n  }\n};\n\nexport default ToastNotification;\nexport { ToastNotification as mdbToastNotification };\n</script>\n<style scoped>\n  .toast {\n    opacity: 1;\n  }\n  .text-muted {\n    padding-left: 10px;\n  }\n</style>\n"],"sourceRoot":""}]);

